@font-face {
  font-family: Avenir-Book;
  src: url(fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir-Heavy;
  src: url(fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 900;
}

.searchField {
  margin-top: 20px;
  position: relative;
}

.input {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  width: 100%;
  font-family: Avenir-Heavy, Arial, sans-serif;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
  outline: none;
}

.bigSearch {
  height: 70px;
  padding-left: 62px;
  line-height: 22px;
  font-size: 16px;
}

.smallSearch {
  height: 42px;
  padding-left: 33px;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
}

.input::placeholder {
  font-weight: 300;
  font-family: Avenir-Book, Arial, sans-serif;
}

.input:focus {
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  outline: none;
}

.icon {
  position: absolute;
  width: 19px;
  height: 19px;
  left: 26px;
  top: 25px;
}

.smallIcon {
  position: absolute;
  width: 11px;
  height: 11px;
  left: 13px;
  top: 15px;
}
